/**
 *  bucket 
 * @param  {Object}   callback   
 * 
 * @param  {function}   callback      
 */
function getService(params, callback) {
    if (typeof params === 'function') {
        callback = params;
        params = {};
    }
    submitRequest.call(this, {
        url: 'http://service.cos.myqcloud.com',
        method: 'GET',
    }, function (err, data) {
        if (err) {
            return callback(err);
        }

        data = data || {};

        if (data && data.ListAllMyBucketsResult
            && data.ListAllMyBucketsResult.Buckets
            && data.ListAllMyBucketsResult.Buckets.Bucket) {
            var buckets = data.ListAllMyBucketsResult.Buckets.Bucket || [];

            if (!(buckets instanceof Array)) {
                buckets = [buckets];
            }
            data.ListAllMyBucketsResult.Buckets = buckets;
        }

        return callback(null, data.ListAllMyBucketsResult);
    });
}

/**
 * Bucket
 * @param  {object}   params     
 *     @param  {string}   params.Bucket     Bucket
 *     @param  {string}   params.Region     
 * @param  {function}   callback      
 * @return  {object}    err        
 * @return  {object}    data    
 *     @return  {Boolean}    data.BucketExist    Bucket
 *     @return  {Boolean}    data.BucketAuth     Bucket 
 */
function headBucket(params, callback) {
    submitRequest.call(this, {
        Bucket: params.Bucket,
        Region: params.Region,
        AppId: params.AppId,
        method: 'HEAD',
    }, function (err, body) {
        if (err) {
            var statusCode = err.statusCode;
            if (statusCode && statusCode == 404) {
                return callback(null, {
                    BucketExist: false,
                    BucketAuth: false
                });
            } else if (statusCode && statusCode == 403) {
                return callback(null, {
                    BucketExist: true,
                    BucketAuth: false
                });
            } else {
                return callback(err);
            }
        }

        return callback(null, {
            BucketExist: true,
            BucketAuth: true
        });
    });
}

/**
 *  Bucket  object 
 * @param  {object}   params     
 *     @param  {string}   params.Bucket     Bucket
 *     @param  {string}   params.Region     
 *     @param  {string}   params.Prefix     
 *     @param  {string}   params.Delimiter       PrefixPrefixdelimiter
 *     @param  {string}   params.Marker       UTF-8marker
 *     @param  {string}   params.MaxKeys       1000
 *     @param  {string}   params.EncodingType       
 * @param  {function}   callback      
 * @return  {object}    err     
 * @return  {object}    data 
 *     @return  {object}    data.ListBucketResult     object 
 */
function getBucket(params, callback) {
    var reqParams = {};
    reqParams['prefix'] = params['Prefix'];
    reqParams['delimiter'] = params['Delimiter'];
    reqParams['marker'] = params['Marker'];
    reqParams['max-keys'] = params['MaxKeys'];
    reqParams['encoding-type'] = params['EncodingType'];

    submitRequest.call(this, {
        method: 'GET',
        Bucket: params.Bucket,
        Region: params.Region,
        AppId: params.AppId,
        qs: reqParams,
    }, function (err, data) {
        if (err) {
            return callback(err);
        }

        data = data || {};

        var contents = data.ListBucketResult.Contents || [];
        var CommonPrefixes = data.ListBucketResult.CommonPrefixes || [];

        if (!(contents instanceof Array)) {
            contents = [contents];
        }

        if (!(CommonPrefixes instanceof Array)) {
            CommonPrefixes = [CommonPrefixes];
        }

        data.ListBucketResult.Contents = contents;
        data.ListBucketResult.CommonPrefixes = CommonPrefixes;

        return callback(null, data.ListBucketResult || {});
    });
}

/**
 *  Bucket
 * @param  {object}   params     
 *     @param  {string}   params.Bucket     Bucket
 *     @param  {string}   params.Region     
 *     @param  {string}   params.ACL        privatepublic-readprivate
 *     @param  {string}   params.GrantRead     x-cos-grant-read: uin=" ",uin=" "
 *     @param  {string}   params.GrantWrite     x-cos-grant-write: uin=" ",uin=" "
 *     @param  {string}   params.GrantFullControl     x-cos-grant-full-control: uin=" ",uin=" "
 * @param  {function}   callback      
 * @return  {object}    err     
 * @return  {object}    data  
 *     @return  {string}    data.Location  
 */
function putBucket(params, callback) {
    var headers = {};
    headers['x-cos-acl'] = params['ACL'];
    headers['x-cos-grant-read'] = params['GrantRead'];
    headers['x-cos-grant-write'] = params['GrantWrite'];
    headers['x-cos-grant-full-control'] = params['GrantFullControl'];
    var appId = this.AppId || '';
    submitRequest.call(this, {
        method: 'PUT',
        Bucket: params.Bucket,
        Region: params.Region,
        AppId: params.AppId,
        headers: headers,
    }, function (err, data) {
        if (err) {
            return callback(err);
        }

        data = data || {};

        return callback(null, {
            Location: getUrl({
                bucket: params.Bucket,
                region: params.Region,
                appId: appId
            })
        });
    });
}

